<template>
  <div id="app">
    <header>
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css" integrity="sha512-iBBXm8fW90+nuLcSKlbmrPcLa0OT92xO1BIsZ+ywDWZCvqsWgccV3gFoRBv0z+8dLJgyAHIhR35VZc2oM/gI1w==" crossorigin="anonymous" referrerpolicy="no-referrer" />
      <h1><span>Vue</span> Music</h1>
    </header>
    <main>
      <section class="player">
        <h2 class="song-title">{{current.title}} - <span>{{current.artist}}</span></h2>
        <div class="controls">
          <button class="prev" @click="prev"><i class="fas fa-chevron-circle-left"></i></button>
          <button class="play" v-if="!playing" @click="play"><i class="fas fa-play-circle"></i></button>
          <button class="pause" v-else @click="pause"><i class="fas fa-pause-circle"></i></button>
          <button class="next" @click="next"><i class="fas fa-chevron-circle-right"></i></button>
        </div>
      </section>
      <section class="playlist">
        <h3>Playlist</h3>
        <button v-for="song in songs" :key="song.src" @click="play(song)" :class="(song.src == current.src) ? 'song playing' : 'song'">
          {{song.title}} - {{song.artist}}
        </button>
      </section>
    </main>
  </div>
</template>

<script>

export default {
  name: 'app',
  data () {
    return {
      current: {},
      index: 0,
      playing: false,
      songs: [
        {
          title: 'Odo Na Eye',
          artist: 'K. Adusei',
          src: require('./assets/odo-na-eye.mp3')
        },
        {
          title: 'Abusua',
          artist: 'K. Adusei',
          src: require('./assets/abusua.mp3')
        }
      ],
      player: new Audio()
    }
  },
  methods: {
    play(song) {
      if (typeof song.src != 'undefined') {
        this.current = song;
        this.player.src = this.current.src;
      }
      this.player.play();
      this.playing = true;
    },
    pause() {
      this.player.pause();
      this.playing = false;
    },
    next() {
      this.index++;
      if(this.index > this.songs.length - 1) {
        this.index = 0;
      }
      this.current = this.songs[this.index];
      this.play(this.current);
    },
    prev() {
      this.index--;
      if(this.index < 0) {
        this.index = this.songs.length - 1;
      }
      this.current = this.songs[this.index];
      this.play(this.current);
    }
  },
  created() {
    this.current = this.songs[this.index];
    this.player.src = this.current.src;
  }
}
</script>

